<!-- eslint-disable -->

<template>
  <div id="app">
    <div class="wrapper">
      <div class="facebook">
        <button class="facebook-login-btn" @click.prevent="loginFacebook">
          facebook
        </button>
        <ul>
          <li v-if="isLoggedIn">{{ user }}</li>
          <li v-if="isLoggedIn">
            <div v-for="post in postsFacebook" :key="post.id" class="post">
              <img v-if="post.full_picture" :src="post.full_picture" />
              <p v-if="post.description">{{ post.description }}</p>
            </div>
          </li>
        </ul>
      </div>
      <div class="instagram">
        <button class="instagram-login-btn" @click="loginInstagram">
          instagram
        </button>
        <ul>
          <li v-if="isLoggedInInstagram">
            <div v-for="post in postsInstagram" :key="post.id" class="post">
              <img v-if="post.media_url" :src="post.media_url" />
              <p v-if="post.caption">{{ post.caption.text }}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')

export default {
  name: 'App',
  data() {
    return {
      isLoggedIn: false,
      isLoggedInInstagram: false,
      user: null,
      postsFacebook: [],
      postsInstagram: [],
    }
  },
  mounted() {
    window.fbAsyncInit = () => {
      if (typeof window.FB === 'undefined') return

      window.FB.init({
        appId: '1664478101039678',
        cookie: true,
        xfbml: true,
        version: 'v10.0', // <- fix the version to a valid one
      })
      window.FB.AppEvents.logPageView()
    }
    ;((d, s, id) => {
      var js,
        fjs = d.getElementsByTagName(s)[0]
      if (d.getElementById(id)) {
        return
      }
      js = d.createElement(s)
      js.id = id
      js.src = 'https://connect.facebook.net/en_US/sdk.js'
      fjs.parentNode.insertBefore(js, fjs)
    })(document, 'script', 'facebook-jssdk')
  },
  methods: {
    loginFacebook() {
      if (typeof window.FB === 'undefined') return
      window.FB.login(this.checkLoginState, {
        scope: 'public_profile,email,user_posts',
      })
    },
    checkLoginState(response) {
      this.statusChangeCallback(response)
    },
    statusChangeCallback(response) {
      console.log(response)
      if (response.status === 'connected') {
        this.isLoggedIn = true
        window.FB.api(
          '/me',
          {
            fields: 'id,name,email',
            access_token: response.authResponse.accessToken,
          },
          (data) => {
            this.user = data
          }
        )
        window.FB.api(
          '/me?fields=posts{description,full_picture}',
          { limit: 10, access_token: response.authResponse.accessToken },
          (data) => {
            console.log(data)
            this.postsFacebook = data.posts.data.filter(
              (item) => item.description || item.full_picture
            )
          }
        )
      } else {
        this.isLoggedIn = false
        this.user = null
      }
    },
    loginInstagram() {
      const url = new URL(window.location.href)
      const clientId = '773337728107206'
      const redirectUri = url.origin + url.pathname
      const scope = 'user_profile,user_media'
      console.log('REQUEST TO INSTAGRAM AUTHORIZATION')
      console.log(
        `SENDING REQUEST TO: ${url}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope}&response_type=code`
      )
      window.open(
        `https://api.instagram.com/oauth/authorize/?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scope}&response_type=code`,
        '_self'
      )
    },
    statusChangeCallbackInstagram(code) {
      const url = new URL(window.location.href)
      const clientId = '773337728107206'
      const redirectUri = url.origin + url.pathname
      const clientSecret = 'd73fb062456e9ff67b314f315c95b1fd'
      const scope = 'user_profile,user_media'
      console.log('REQUEST TO GET ACCESS TOKEN')
      console.log(
        `SENDING REQUEST TO: https://api.instagram.com/oauth/access_token`
      )
      console.log(
        `DATA: client_id=${clientId}&client_secret=${clientSecret}&grant_type=authorization_code&redirect_uri=${redirectUri}&code=${code}`
      )
      console.log(
        'full link',
        `https://api.instagram.com/oauth/access_token?client_id=${clientId}&client_secret=${clientSecret}&grant_type=authorization_code&redirect_uri=${redirectUri}&code=${code}`
      )

      const params = new URLSearchParams()
      params.append('client_id', clientId)
      params.append('client_secret', clientSecret)
      params.append('grant_type', 'authorization_code')
      params.append('redirect_uri', redirectUri)
      params.append('code', code)

      axios
        .post('https://api.instagram.com/oauth/access_token', params, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
          },
        })
        .then((res) => {
          console.log('RESPONSE FROM INSTAGRAM:')
          console.log(res.data)
          const accessToken = res.data.access_token
          const userId = res.data.user_id
          console.log('REQUEST TO GET ALL POSTS')
          console.log(
            `SENDING REQUEST TO: https://graph.instagram.com/${userId}/media`
          )
          console.log(
            `DATA: fields=id,media_url,caption&access_token=${accessToken}`
          )
          axios
            .get(`https://graph.instagram.com/me/media`, {
              params: {
                fields: 'id,media_url,caption',
                access_token: accessToken,
              },
            })
            .then((res) => {
              this.isLoggedInInstagram = true
              console.log('RESPONSE FROM INSTAGRAM:')
              console.log(res.data)
              this.postsInstagram = res.data.data.filter(
                (item) => item.media_url && item.caption
              )
            })
            .catch((err) => {
              console.log(err)
            })
        })
        .catch((err) => {
          console.log(err)
        })
    },
  },
  created() {
    const url = new URL(window.location.href)
    const params = new URLSearchParams(url.search)
    const code = params.get('code')
    if (code) {
      console.log(code)
      this.statusChangeCallbackInstagram(code)
    }
  },
}
</script>

